.footer {
    background: #000;
    height: 90px;
    width: 100vw;
    position: fixed;
    bottom: 0;
    z-index: 99;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2.34375vw;

    .plus18 {
        position: absolute;
        right: 18vw;
    }
    a {
        width: auto;
        height: auto;
       img {
           height: 100%;
       }
    }
    .money {
        width: 33vw;
        a {
            margin-right: 1vw;
            img {
                filter: brightness(3);
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }
    .app {
        width: 33vw;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translateY(2px);
        a {
            margin-right: 15px;
            width: auto;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    .links {
        width: 33vw;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        a {
            margin-right: 1vw;
            &:last-child {
                margin-right: 0;
            }
        }
    }
}