                    position: absolute;
                    content: '';
                    display: block;
                    background: #fff;
                    width: 0.078125vw;
                    height: 0.78125vw;
                    top:0.6640625vw;
                    right: 1.3671875vw;
                    -webkit-transform: skew(16deg);
                    -moz-transform: skew(16deg);
                    -o-transform: skew(16deg);
                }
