                    position: absolute;
                    content: '';
                    display: block;
                    background: #fff;
                    width: 0.78125vw;
                    height: 0.078125vw;
                    top:0.6640625vw;
                    right: 1.3671875vw;
                    -webkit-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
                    -moz-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
                    -o-transform: skew(16deg) translate(0.25vw, 0.3515625vw);
