            &:before {
                content: '';
                display: block;
                height: 100%;
                width: 100%;
                
                -webkit-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
                -moz-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
                    -o-transform: skew(-16deg) translate(0.3515vw, 0.3515vw);
