.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2vh 6vw 2vh 6vw;
   overflow: hidden;
   .logo {
    width: 11.5vw;
   }
    .plus-top {
        position: absolute;
        top: 0.9027777777777778vh;
        left: calc(50% - 12vw);
        width: 24vw;
    }
    .best {
        display: flex;
        align-items: center;
        font-family: Parimatch-Bold;
        font-size: 0.8984375vw;
       
        span {
            font-size: 1.156171875vw;
        }
        .best-img {
            margin: 0 20px;
            width: 3.515625vw;
        }
        &-1 {
            text-align: end;
           
        }
    }
    
}